import React,{Component} from 'react';
import {Navbar, NavbarBrand, Nav ,NavbarToggler,Collapse,NavItem,
    Jumbotron,Button,Modal,ModalBody,ModalHeader,Form,FormGroup,Label,Input} from 'reactstrap';
import {NavLink} from 'react-router-dom';
import './styling.css';
class Header extends Component{

    constructor(props){
        super(props);
            this.state={
                isNavOpen:true,
                isModalOpen:false
            };
            this.toggleNav=this.toggleNav.bind(this);
            this.toggleModal=this.toggleModal.bind(this);
            this.handleLogin=this.handleLogin.bind(this);
    }

    toggleNav(){
        this.setState({
            isNavOpen: !this.state.isNavOpen
        });
    }
    
    toggleModal(){
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }

    handleLogin(event){
        this.toggleModal();
        alert("USername: "+ this.username.value + " PAssword : "+ this.password.value +" Remember ")
    + this.remember.value}


    render(){
        return(
            <React.Fragment>
         <Navbar dark expand ="md">
          <div className="container">
              <NavbarToggler onClick={this.toggleNav}/>
            <NavbarBrand className="mr-auto" href="/" >
                <img src="assets/images/logo.png" height="80" className="rounded-circle" width="130" alt="Ristronte ConFusion"/>
                
            </NavbarBrand>
           <Collapse isOpen={this.state.isNavOpen} Navbar>
                <Nav navbar>
                <NavItem>
                    <NavLink className="nav-link" to ="/home">
                        <span className="fa fa-home fa-lg"></span> Home
                        
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink className="nav-link" to ="/aboutus">
                        <span className="fa fa-user fa-lg" padding="0.5px"></span> About us
                        
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink className="nav-link" to ="/menu">
                        <span className="fa fa-bars fa-lg "></span> Menu
                        
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink className="nav-link" to ="/contactus">
                        <span className="fa fa-phone fa-lg"></span> Contact us
                        
                    </NavLink>
                </NavItem>
            </Nav>
            
            <Nav className="ml-auto" navbar>
                                <NavItem>
                                    <Button outline onClick={this.toggleModal}><span className="fa fa-sign-in fa-lg"></span> Login</Button>
                                </NavItem>
                            </Nav>
            </Collapse>
          </div>
         
        </Navbar>
            <Jumbotron>
            <div className="container">
                <div className="row row-header">
                    <div className="col-12 col-sm-6">
                        <h1>Fitness App</h1>
                        <p>Things work out best for those who make the best of how things work out </p>
                    </div>
                </div>
                </div>
            </Jumbotron>

            <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
                    <ModalBody>
                    <Form onSubmit={this.handleLogin}>
                            <FormGroup>
                                <Label htmlFor="username">Username</Label>
                                <Input type="text" id="username" name="username"
                                    innerRef={(input) => this.username = input} />
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="password">Password</Label>
                                <Input type="password" id="password" name="password"
                                    innerRef={(input) => this.password = input}  />
                            </FormGroup>
                            <FormGroup check>
                                <Label check>
                                    <Input type="checkbox" name="remember"
                                    innerRef={(input) => this.remember = input}  />
                                    Remember me
                                </Label>
                            </FormGroup>
                            <Button type="submit" value="submit" color="primary">Login</Button>
                        </Form>
                    </ModalBody>
                </Modal>
            </React.Fragment>
        );
    }
}

export default Header;


